import { TWarehouseQuery } from "src/types/types";
import imgs from "src/data/warehouseImgs";

export const findImg = (data: TWarehouseQuery) => {
  const dark = window.matchMedia("(prefers-color-scheme: dark)").matches;

  return imgs.find(u =>
    dark ? u.includes(`${String(data?.id)}D`) : u.includes(String(data?.id)),
  );
};
